<template>
  <div class="case-add">
      <el-form :model="formData" label-width="80px">
          <el-form-item label="项目名称">
              <el-input v-model="formData.title"></el-input>
          </el-form-item>
          <el-form-item label="施工内容">
              <el-input v-model="formData.content"></el-input>
          </el-form-item>
          <el-form-item label="工程地点">
              <el-input v-model="formData.adress"></el-input>
          </el-form-item>
          <el-form-item label="上传图片">
              <el-upload
                class="avatar-uploader"
                action="https://upload-z1.qiniup.com"
                :show-file-list="false"
                :on-success="upload"
                :data="{token:token}">
                <img v-if="formData.img" :src="formData.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
          </el-form-item>
          <el-form-item>
              <el-button @click="upData" v-if="$route.name == '工程添加页'">提交</el-button>
              <el-button @click="edit" v-if="$route.name == '工程修改页'">保存修改</el-button>
          </el-form-item>
      </el-form>
  </div>
</template>
<script>
export default {
  data() {
      return {
          formData:{
              title:'',
              content:'',
              adress:'',
              img:''
          },
          token:''
      }
  },
  methods:{
      upload(res) {
        //   console.log(res)
          this.formData.img = "http://image.yaojunrong.com/" + res.key
      },
      getToken() {
          this.$axios.qiniuGet().then(res => {
            //   console.log(res)
            this.token = res.data
          })
      },
      upData() {
          this.$axios.post('addCases',this.formData).then(res => {
              if(res.code == 200) {
                  this.$message({
                    message: '恭喜你，提交成功！',
                    type: 'success'
                  });
                  setTimeout(() => {
                    this.$router.push('/case')
                  },1000)
              }
          })
      },
      getIdData() {
          this.$axios.get('getCases',{_id:this.$route.query.id}).then(res => {
              this.formData = res.data[0]
          })
      },
      edit() {
          this.$axios.post('updateCases',this.formData).then(res => {
              if(res.code == 200) {
                  this.$message({
                    message: '恭喜你，修改成功！',
                    type: 'success'
                  });
                  setTimeout(() => {
                    this.$router.push('/case')
                  },1000)
              }
          })
      }
  },
  created() {
      this.getToken()
      if(this.$route.name == '工程修改页') {
          this.getIdData()
      }
  },
  watch:{
      $route(to,from,next) {
          if(to.name == '工程添加页'){
              this.formData = {
                    title:'',
                    content:'',
                    adress:'',
                    img:''
              }
          }
      }
  }
//   watch:{
//       if()
//   }
}
</script>
<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
<style scoped>
.case-add{
    padding: 20px;
}
</style>



